import React,{useContext, useEffect, useState} from 'react';
import {Myrecipe} from './Myrecipe';
import { Link } from 'react-router-dom';
import "../css/Mypage.scss";


const Like = ({val}) => {
    
    const {setScroll} = useContext(Myrecipe);

    //like creatorId  isOwner( "cYTXrL5LTQR4rxs3TuaIlgfCQPw1") 
    //  like  data id  like data  !
    
    return (
        <div className='recipe-wrap'>  
            {
                val.length == 0 ?
                <div className='like-list-n'>
                    <p>  </p>
                    <Link to="../list">  </Link>
                </div>
                :
                val.map((obj)=>{
                    return(
                        <div className='recipe-box'>
                            <Link to={`../detail/${obj.name}`}>
                            <img src={obj.mainImg}/>
                            <p className='name'>{obj.name}</p>
                            <p>#{obj.hashtag[0]} #{obj.hashtag[1]} #{obj.hashtag[2]}</p>
                            <button onClick={setScroll(0)}> </button>
                            </Link>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Like